:root {
    --color-light:  #f3f7f7;
    --color-light-2: #e7eeef;
    --color-dark: #8b999b;
    --color-main: #1ba94c; 
}
body {
    height: fit-content;
    overflow-x: hidden;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.search {
    width: 100%;
    height: 400px;
    background-image: url('../img/arabic\ \(3\).svg');
    background-size: 500px 500px;
    background-repeat: repeat;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
}
.search h1 {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 30px;
    color: #0e141e;
}
.search h1 span {
    font-family: 'Reem Kufi', sans-serif;
    user-select: none;
    -moz-user-select: none;
    color: black;
}
.search div {
    max-width: 600px;
    margin-left: calc(50% - 300px);
}
.search input {
    border: none;
    outline: none;
    width: 100%;
    font-size: 30px;
    background-color: var(--color-dark);
    border-radius: 5px;
    height: 55px;
    text-align: right;
    color: #eee;
    box-shadow: #0e141e 0px 1px 10px 3px;
    transition: 0.4s all ease;
}
.search input:hover, .search input:focus {
    box-shadow: #0e141e 0px 0px 6px 3px;
}
.search div form button {
    background-color: #0e141e;
    background-image: url('../img/loupe.svg');
    background-repeat: no-repeat;
    background-size: 33px 33px;
    display: block;
    height: 55px;
    position: relative;
    top: -55px;
    z-index: 5565;
    width: 50px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-position: center center;
    border: none;
    outline: none;
    cursor: pointer;
}
::placeholder {
    color: #eee;
}
::-moz-placeholder {
    color: #eee;
}
@media (max-width: 760px) {
    .search {
        font-size: 14px;
    }
    .search div {
        max-width: 90%;
        margin-left: calc(50% - 45%);
    }
}