footer {
    width: 100%;
    background-color: rgb(19, 19, 19);
    height: 200px;
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 0;
}
footer .foot {
    height: 100%;
    width: fit-content;
    position: absolute;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
footer .foot span {
    color: #eee;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
    font-family: 'Staatliches', cursive !important;
}
footer .foot svg {
    width: 45px;
    height: 45px;
}
footer .foot img {
    width: 54px;
    height: 54px;
}
.copy {
    width: 100%;
    background-color: rgb(3, 3, 3);
    color: #eee;
    height: fit-content;
    text-align: center;
    padding: 10px;
    font-size: 18px;
}