.new-list .number-of-total {
    font-family: 'Tajawal', sans-serif;
    width: 100%;
    text-align: center;
    padding: 15px;
    font-size: 26px;
    font-weight: 500;
}
.next-pre {
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.next-pre button {
    border: none;
    outline: none;
    border-radius: 4px;
    background-color: #1ba94c;
    color: white;
    font-size: 30px;
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.next-pre span {
    width: 40px;
    height: 40px;
    color: white;
    background-color: #1ba94c;
    margin: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.loading {
    position: absolute;
    width: 100% !important;
    height: 410px !important;
    box-shadow: none !important;
    background-color: rgba(92, 92, 92, 0.5) !important;
    left: 0;
}
.loading span {
    width: 75px;
    height: 75px;
    border: 7px solid #1ba94c;
    border-radius: 50%;
    border-top: 7px solid #f3f3f3;
    animation: load 1.1s linear infinite;
    background-color: transparent !important;
}
@keyframes load {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
body::-webkit-scrollbar {
    width: 1.25em;
    background-color: rgb(243, 243, 243);
}
body::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    border-radius: 10px;
    border: rgb(243, 243, 243) 5px solid;
}
body::-webkit-scrollbar-thumb:hover {
    background-color: rgb(145, 145, 145);
}