.err {
    position: absolute;
    width: 100%;
    left: 0;
    display: flex;
    justify-content: center;
    align-content: space-around;
    background-color: white !important;
    box-shadow: none !important;
    border-radius: 0px !important;
}
.err img {
    min-width: 200px;
    max-width: 800px;
}
.err h2 {
    border: none;
    width: 100%;
    font-size: 26px;
    text-align: center;
    margin: 0;
}