
.book-info {
    min-width: 280px;
    max-width: 800px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: fit-content;
    position: absolute;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    margin-right: 50px;
    right: 0;
}
.book-info img {
    width: 175px;
    border-radius: 2px;
    border: 4px solid rgb(33, 169, 223);
    margin-bottom: 10px;
}
.book-info .grid {
    text-align: right;
    height: fit-content;
    margin-top: 0;
    margin-right: 10px;
    width: 100%;
}
.book-info .grid h1, .book-info .grid h2 {
    padding: 5px;
}
.book-info .grid h1 {
    text-align: center !important;
}
.book-info .grid p {
    font-size: 22px;
    font-weight: 500;
    letter-spacing: 1px;
    word-spacing: 1.2px;
    padding-top: 5px;
}
strong {
     font-size: 26px;
     color: rgb(33, 169, 223);
}
.book-info button {
    margin-top: 20px;
    background-color: rgb(33, 169, 223);
    border: none;
    outline: none;
    color: #eee;
    padding: 6px 16px;
    font-size: 24px;
    font-weight: bold;
    border-radius: 6px;
    margin-bottom: 20px;
    border-bottom: solid 5px rgb(31, 139, 182);
    transition: 0.5s all ease-out;
}
.book-info button:focus {
    border-bottom: none;
}
@media (min-width: 280px) and (max-width: 760px) {
    .book-info {
        margin-right: 0;
    }
}