:root {
    --color-light:  #f3f7f7;
    --color-light-2: #e7eeef;
    --color-dark: #738f93;
    --color-main: #1ba94c; 
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Markazi Text', serif;
}
.mobile-menu {
    display: none;
    border: none;
    outline: none;
    font-size: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    font-size: 90px;
    line-height: 40px;
}
.mobile-menu img {
    width: 40px;
}
header {
    width: 100%;
    height: 75px;
    display: flex;
    position: sticky;
    top: 0%;
    z-index: 9999999;
    justify-content: space-evenly;
    align-items: center;
    background-color: var(--color-light-2);
    box-shadow: var(--color-dark) 0px 1px 12px 0px;
}
header nav {
    width: 80%;
    height: 100%;
    font-size: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
}
header nav ul {
    display: flex;
    flex-wrap: wrap;
    width: 60%;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
header nav ul li , header nav ul li a {
    transition: color 0.4s ease;
    height: 100%;
    display: flex;
    align-items: center;
    user-select: none;
    -moz-user-select: none;
    text-decoration: none;
    color: black;
}
header nav ul li a:hover {
    color: var(--color-dark);
    border-bottom: var(--color-main) solid 4.5px;
    align-self: flex-end;
    height: 94%;
}
.my {
    border-bottom: var(--color-main) solid 4.5px;
    align-self: flex-end;
    height: 94%;
}
header svg {
    width: 55px;
    height: 55px;
}
.mobile {
    display: none;
    position: absolute;
    left: 0;
    width: 70%;
    height: 500px;
    background-color: var(--color-light-2);
    z-index: 99999999;
    top: 65px;
    left: -70%;
    transition: 0.4s all ease-out;
    flex-wrap: wrap-reverse;
}
.mobile a {
    text-decoration: none;
    font-size: 26px;
    width: 100%;
    margin: 5% 5%;
    text-align: right;
    transition: all 0.4s ease;
    color: black;
}
.mobile a:hover {
    color: var(--color-dark);
    transform: translate(-15px);
}
@media (max-width: 760px) {
    header nav {
        display: none;
    }
    .mobile-menu {
        display: block;
    }
    .mobile {
        display: flex;
    }
}