.top {
    position: sticky;
    left: 90%;
    top: 85%;
    z-index: 9999999;
    width: 50px;
    height: 50px;
    background-color: #1ba94c;
    border: none;
    outline: none;
    border-radius: 0px;
    justify-content: center;
    align-items: flex-end;
    display: flex;
    opacity: 0;
    transition: 0.4s all ease-in-out;
    pointer-events: none;
}
.top span {
    color: white;
    font-size: 85px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 23px;
    transition: all 0.3s ease;
}
.top:hover{
    background-image: linear-gradient(to top, #d0d4d0, #1ba94c, #1ba94c);
    background-image: -moz-linear-gradient(to top, #d0d4d0, #1ba94c, #1ba94c);
    background-image: -webkit-linear-gradient(to top, #d0d4d0, #1ba94c, #1ba94c);
}
.top span:hover {
    color: #d0d4d0;
}
.show {
    opacity: 1;
    cursor: pointer;
    pointer-events: all;
}