:root {
    --color-light:  #f3f7f7;
    --color-light-2: #e7eeef;
    --color-dark: #8b999b;
    --color-main: #1ba94c; 
}
.new-list {
    width: 100%;
    height: fit-content;
    min-height: 400px;
}
.new-list h2 {
    text-align: right;
    margin-top: 40px;
    margin-left: 80%;
    font-size: 35px;
    word-spacing: 3px;
    letter-spacing: 1.5px;
    border-bottom: var(--color-main) 4.5px solid;
    width: 164px;
    line-height: 45px;
}
#books {
    width: 80%;
    margin-left: calc(50% - 40%);
    display: grid;
    margin-top: 40px;
    grid-template-columns: repeat(5, 1fr);
    gap: 30px 5%;
    margin-bottom: 30px;
}
#books div {
    display: flex;
    flex-wrap: wrap;
    background-color: var(--color-light-2);
    justify-content: center;
    align-content: space-around;
    text-align: center;
    box-shadow: #8b999b 4px 4px 9px 4px;
}
#books div img {
    padding-top: 15px;
    width: 110px;
}
#books div h3 {
    width: 100%;
    font-size: 20px;
    padding: 10px;
    max-height: 70px;
    word-break: break-word;
    text-overflow: ellipsis;
    overflow: hidden;
    overflow-wrap: break-word;
    letter-spacing: 1.3px;
}
#books div h4 {
    width: 100%;
    color: var(--color-main);
    font-size: 18px;
    padding: 10px;
    max-height: 50px;
    word-break: break-word;
    text-overflow: ellipsis;
    text-transform: capitalize;
    overflow: hidden;
    overflow-wrap: break-word;
    letter-spacing: 1.4px;
}
#books div a button {
    padding: 3px 24px;
    margin-bottom: 5px;
    font-size: 21px;
    background-color: var(--color-main);
    color: var(--color-light);
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 3px;
}
.more {
    border: black 3px solid;
    padding: 10px;
    cursor: pointer;
    font-family: 'Tajawal', sans-serif;
    font-size: 20px;
    font-weight: 500;
    margin-left: calc(50% - 64.5px);
    margin-bottom: 20px;
    margin-top: 20px;
    background-color: transparent;
    transition: all 0.3s ease-out;
}
.more:hover {
    background-color: black;
    color: white;
}
@media (max-width: 560px) and (min-width: 280px){
    .new-list h2 {
        margin-left: calc(50% - 82px);
        margin-top: 30px;
    }
    #books {
        grid-template-columns: repeat(2, 1fr);
        width: 95%;
        margin-left: calc(50% - 47.5%);
    }
}
@media (max-width: 820px) and (min-width: 561px){
    .new-list h2 {
        margin-left: calc(50% - 82px);
        margin-top: 30px;
    }
    #books {
        grid-template-columns: repeat(3, 1fr);
        width: 95%;
        margin-left: calc(50% - 47.5%);
    }
}
@media (max-width: 1000px) and (min-width: 821px){
    #books {
        grid-template-columns: repeat(4, 1fr);
        width: 90%;
        margin-left: calc(50% - 45%);
    }
}